<template>
  <table width='100%'
         cellspacing="0"
         cellpadding="0"
         border-spacing="0"
         id="plice_table"
         class="table">
    <thead style="line-height:50px;">
      <th class="aaa">酒店名称</th>
      <th class="bbb">提成比例</th>
      <th class="ccc">业务员</th>
      <th class="ddd">金额</th>
    </thead>
    <tbody>
      <tr style="color:#000;font-weight:bold;">
        <td class="aaa">总计</td>
        <td class="bbb">{{ businessCommissionTotal }}</td>
        <td class="ccc">-</td>
        <td class="ddd">{{ commissionTotal }}</td>
      </tr>
      <tr v-for=" (item,i) in arrList"
          :key="i"
          :class="{i}">
        <td class="aaa">{{ item.hotel.hotelName }}</td>
        <td class="bbb">{{ item.businessCommission }}</td>
        <td class="ccc">{{ item.businessManager.realName }}</td>
        <td class="ddd">{{ item.commission }}</td>
      </tr>
    </tbody>
  </table>
</template>
<script>
import { GetBusinessBsmBillRecordDetail } from '@/api/api_business_bsm';
export default {
    data() {
        return {
            arrList: [],
            dataForm: {
                billTime: this.$route.query.billTime ? this.$route.query.billTime : '',
                userId: this.$route.query.userId ? this.$route.query.userId : ''
            },
            businessCommissionTotal: 0,
            commissionTotal: 0
        };
    },
    created() {
        this.$store.commit('setHeader', this.$route.query.billTime + '业务提成');
        this.getDetails();
    },
    methods: {
        // 查询业务提成详情
        getDetails() {
            GetBusinessBsmBillRecordDetail(this.dataForm).done((res) => {
                this.arrList = res.data;
                this.businessCommissionTotal = this.arrList.reduce(function (total, item) {
                    return total + item.businessCommission;
                }, 0);
                this.commissionTotal = this.arrList.reduce(function (total, item) {
                    return total + item.commission;
                }, 0);
            });
        }
    }
};
</script>
<style scoped>
.table {
    color: #595959;
    font-size: 0.8rem;
    table-layout: fixed; /* (允许设置列宽,通过th)  默认为 table-layout: automatic 默认每一列列宽均分(不可设置列宽) */
}
thead {
    color: #000;
}
.table tbody tr:nth-child(odd) {
    background: #fff;
}
.table tbody tr:nth-child(even) {
    background: #f8f8f8;
}
tr {
    color: #000;
    line-height: 50px;
}
td {
    border-bottom: 1px solid #ece9ec;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.aaa {
    width: 30%;
    text-align: left;
    padding-left: 20px;
}
.bbb {
    width: 23;
}
.ccc {
    width: 23;
}
.ddd {
    width: 23;
}
</style>


